:root {
  --main-red-color: rgb(230, 46, 77);
  --light-blue: rgb(104, 207, 225);
}
.primary-text {
  color: var(--light-blue);
}
* {
  box-sizing: border-box;
}

body {
  font-family: "Raleway", sans-serif;
}
a,
a:hover {
  text-decoration: none;
  color: inherit;
}

.btn-outline-primary {
  color: #fff;
  border-color: #fff;
}
.btn-outline-danger {
  color: #fff;
  border-color: #fff;
}
.bg-primary {
  background-color: var(--main-red-color) !important;
  opacity: 0.6;
}

.btn-round {
  border-radius: 30px;
}
.bg-indigo {
  background: indigo;
}
.text-cyan {
  color: #35bdff;
}
.split-content {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}

.sub-content {
  margin-top: 100px;
}

.bg-building {
  background: url("../img/ileys-tower.jpg") no-repeat center center;
  background-size: cover;
  position: relative;
  height: 100vh;
}

.bg-building::before {
  content: "";
  position: absolute;
  height: 100vh;
  width: 100%;
  background: rgba(196, 85, 85, 0.6);
}
.bg-soap {
  background: url("../img/Factory.jpg") center center no-repeat;
  background-size: cover;
  height: 100vh;
  position: relative;
}
.bg-soap::before {
  content: "";
  position: absolute;
  height: 100vh;
  width: 100%;
  background: rgba(59, 75, 167, 0.6);
}
#loader-wrapper {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1000;
}
#loader {
  display: block;
  position: relative;
  left: 50%;
  top: 50%;
  width: 150px;
  height: 150px;
  margin: -75px 0 0 -75px;
  border: 3px solid transparent;
  border-top-color: #35bdff;
  border-radius: 50%;

  -webkit-animation: spin 2s linear infinite;
  animation: spin 2s linear infinite;
}
.center-image {
  position: absolute;
  left: 50%;
  top: 50%;
  margin-left: -70px;
  margin-top: -26px;
  display: block;
  -webkit-animation: fadeout 3s ease-in-out infinite;
  animation: fadeout 3s ease-in-out infinite;
}
#loader:before {
  content: "";
  position: absolute;
  top: 5px;
  left: 5px;
  right: 5px;
  bottom: 5px;
  border-radius: 50%;
  border: 3px solid transparent;
  border-top-color: #ff3535;
  -webkit-animation: spin 3s linear infinite;
  animation: spin 3s linear infinite;
}
#loader:after {
  content: "";
  position: absolute;
  top: 15px;
  left: 15px;
  right: 15px;
  bottom: 15px;
  border-radius: 50%;
  border: 3px solid transparent;
  border-top-color: #fcff62;
  -webkit-animation: spin 1.5s linear infinite;
  animation: spin 1.5s linear infinite;
}
.content{
  opacity: 0;
}

.transparent {
  background-color: transparent !important;
}

.fadeout {
  -webkit-animation: fadeout 2s ease;
  -moz-animation: fadeout 2s ease;
  -o-animation: fadeout 2s ease;
  -ms-animation: fadeout 2s ease;
  animation: fadeout 2s ease;
}
.fadein {
  visibility: visible;
  -webkit-animation: fadein 2s ease;
  -moz-animation: fadein 2s ease;
  -o-animation: fadein 2s ease;
  -ms-animation: fadein 2s ease;
  animation: fadein 2s ease;
}
.hide {
  display: none;
}
.show {
  visibility: visible;
}

html.sr .load-hidden {
  visibility: hidden;
}
footer {
  position: relative;
  border: 3px solid transparent;
}

/* Animation key frames */
@-webkit-keyframes fadeout {
  0% {
    opacity: 1;
  }
  75% {
    opacity: 0.3;
  }
  100% {
    opacity: 0;
  }
}
@keyframes fadeout {
  0% {
    opacity: 1;
  }
  75% {
    opacity: 0.3;
  }
  100% {
    opacity: 0;
  }
}
@-moz-keyframes fadeout {
  0% {
    opacity: 1;
  }
  75% {
    opacity: 0.3;
  }
  100% {
    opacity: 0;
  }
}
@-o-keyframes fadeout {
  0% {
    opacity: 1;
  }
  75% {
    opacity: 0.3;
  }
  100% {
    opacity: 0;
  }
}
@-webkit-keyframes fadein {
  0% {
    opacity: 0;
  }
  50% {
    opacity: 0.8;
  }
  100% {
    opacity: 1;
  }
}
@keyframes fadein {
  0% {
    opacity: 0;
  }
  50% {
    opacity: 0.8;
  }
  100% {
    opacity: 1;
  }
}
@-moz-keyframes fadein {
  0% {
    opacity: 0;
  }
  50% {
    opacity: 0.8;
  }
  100% {
    opacity: 1;
  }
}
@-o-keyframes fadein {
  0% {
    opacity: 0;
  }
  50% {
    opacity: 0.8;
  }
  100% {
    opacity: 1;
  }
}
@-webkit-keyframes spin {
  0% {
    -webkit-transform: rotate(0deg);
    -ms-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
    -ms-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}

@keyframes spin {
  0% {
    -webkit-transform: rotate(0deg);
    -ms-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
    -ms-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}
@-webkit-keyframes bounce {
  0% {
    transform: translate(-50%, 5px) rotate(90deg);
  }
  100% {
    transform: translate(-50%, 10px) rotate(90deg);
  }
}

@keyframes bounce {
  0% {
    transform: translate(-50%, 5px) rotate(90deg);
  }
  100% {
    transform: translate(-50%, 10px) rotate(90deg);
  }
}

/* Animation Frames */
@media (min-width: 768px) {
  .h-md-100 {
    height: 100vh;
  }
}
